import React from 'react'
import {ProfileInfo,ProfileEditDocCard, ProfileDetailCards} from '../components/profile'
import {
  ProfileDetail,
  FamilyDetail,
  EducationDetail,
  WorkExpDetail,
  SocialDetail,
  ReffrenceDetail,
  PolicyDetails,
  IdCard,
  DocumentDetail,
  BankDetail,
 } from '../components/modal/profile'
const Profile = () => {
  return (
    <>
        <ProfileInfo/>
        <ProfileEditDocCard/>
        <ProfileDetailCards/>
        <ProfileDetail/>
        <FamilyDetail/>
        <EducationDetail/>
        <WorkExpDetail/>
        <SocialDetail/>
        <ReffrenceDetail/>
        <PolicyDetails/>
        <DocumentDetail/>
        <BankDetail/>
        <IdCard/>
        
      
    </>
  )
}

export default Profile
